.site-header {
  border: 0;
  .wrapper {
    padding: 50px 0;
    text-align: center;
    @include media-query($on-palm) {
      padding: 20px 0;
    }
  }
}

.site-title {
  float: none;
  font-family: $title-font-family;
  font-weight: 900;
  font-size: 35px;
  color: $grey-color-dark;
  text-transform: uppercase;
  letter-spacing: 1px;
  @include media-query($on-palm) {
    font-size: 30px;
  }
}

.site-subtitle{
  margin-top: 60px;
  span {
    display: block;
  }
}
.site-subtitle-quote {
  font-family: $base-font-family;
  font-size: 18px;
  font-weight: 400;
  @include media-query($on-palm) {
    font-size: 16px;
  }
}
.site-subtitle-author {
  font-size: 16px;
  font-weight: 700;
  @include media-query($on-palm) {
    font-size: 14px;
  }
}

.post-link,
.post-link:visited {
  position: relative;
  top: 0;
  left: 0;
  color: $grey-color-dark;
  font-size: 24px;
  font-weight: 900;
  text-shadow: 0px 0px 0px $background-color;
  transition: top 0.2s, left 0.2s, text-shadow 0.2s;
  @include media-query($on-palm) {
    font-size: 25px;
    line-height: 1;
  }
}

.post-link:hover {
  color: grey;
  text-decoration: none;
}

.post-navigation {
  font-size: 14px;
  display: block;
  width: auto;
  overflow: hidden;
}

.post-navigation a {
  display: block;
  width: 50%;
  float: left;
  margin: 1em 0;
}

.post-navigation .post-prev {
  text-align: left;
}

.post-navigation .post-next {
  text-align: right;
}

.social-media-list {
  margin: 30px 0;
  text-align: center;
  @include media-query($on-palm) {
    margin: 15px 0;
  }
  li {
    display: inline-block;
    margin: 0 15px;
    svg {
      width: 40px;
      height: 40px;
    }
    &:hover {
      svg path {
        fill: $brand-color;
      }
    }
  }
}

