// Labels */
.label {
  display: inline;
  padding: .25em .6em;
  font-size: 75%;
  font-weight: 500;
  line-height: 1;
  color: #ffffff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: .25em;
}
.label-info {
  background-color: #5bc0de;
}

.label-info[href]:hover,
.label-info[href]:focus {
  background-color: #31b0d5;
}
.label[href]:hover,
.label[href]:focus {
  color: #ffffff;
  text-decoration: none;
  cursor: pointer;
}

.label-default {
  background-color: #999999;
}

.label-default[href]:hover,
.label-default[href]:focus {
  background-color: #808080;
}

.label-danger {
  background-color: #d9534f;
}

.label-danger[href]:hover,
.label-danger[href]:focus {
  background-color: #c9302c;
}

.label-success {
  background-color: #5cb85c;
}

.label-success[href]:hover,
.label-success[href]:focus {
  background-color: #449d44;
}

.label-warning {
  background-color: #f0ad4e;
}

.label-warning[href]:hover,
.label-warning[href]:focus {
  background-color: #ec971f;
}

// Meters */
.meter { 
  height: 24px;  // Can be anything */
  position: relative;
  border: 1px solid #f1f1f1;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  margin-bottom: 1em;
}
.meter > span {
  display: block;
  height: 100%;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;

  position: relative;
  overflow: hidden;
}
.meter > span  > span{
  margin-left: 4px;
  color: #fff;
}

.no-list{
  margin: 0px;
  padding: 0px;
  list-style: none;
}

// WorkExperience */
.work li{
  border-bottom: 1px solid #f1f1f1;
  margin-bottom: 2em;
}
.work li span{
  float: right;
}
.work li h5{
  float: left;
}
.work li p{
  clear: both;
}

.clearfix:after { 
 content: "."; 
 visibility: hidden; 
 display: block; 
 height: 0; 
 clear: both;
}

h1, h2, h3, h4, h5, h6 {
    font-family:'Open Sans', sans-serif;
    color:#444;
}

h1 {
    margin: 0;
    font-size: 3.00em;
    line-height: 1.2em;
    margin-bottom: 0.4em;
} // 60px / 72px */

h2 {
    margin: 0;
    font-size: 2.50em;
    line-height: 1em;
    margin-bottom: 0.5em;
} // 48px / 48px */

h3 {
    margin: 0;
    font-size: 2.00em;
    line-height: 1.3333333333333333333333333333333em;
    margin-bottom: 0.6667em;
} // 36px / 48px */

h4 {
    margin: 0;
    font-size: 1.5em;
    line-height: 1em;
    margin-bottom: 1em;
} // 24px / 24px */

h5 {
    margin: 0;
    font-size: 1em;
    line-height: 1.1428571428571428571428571428571em;
    margin-bottom: 1.1428571428571428571428571428571em;
} // 21px / 24px */

h6 {
    margin: 0;
    font-size: 0.8em;
    line-height: 1.3333333333333333333333333333333em;
    margin-bottom: 1.3333333333333333333333333333333em;
} // 18px / 24px */

p, blockquote, pre, td, th, label {
    margin: 0;
    font-size: 1em;
    line-height: 1.5em;
    margin-bottom: 1.5em;
} // 16px / 24px */

small, p.small {
    margin: 0;
    font-size: 0.875em;
    line-height: 1.7142857142857142857142857142857em;
    margin-bottom: 1.7142857142857142857142857142857em;
} // 14px / 24px 

.break {
  padding-bottom: 1em;
  clear: both;
}

