@charset "utf-8";



// Our variables
// custom font
//@import url("https://fonts.googleapis.com/css?family=Lora:400,700");
//@import url("https://fonts.googleapis.com/css?family=Titillium+Web:400,300,700,900");
@import url("/assets/css/Lora.css");
@import url("/assets/css/Titilium.css");

$title-font-family: "Titillium Web", Helvetica, Arial, sans-serif;

// Update this line
//$base-font-family: "Lora", Helvetica, Arial, sans-serif;
$base-font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
$base-font-size:   16px;
$base-font-weight: 400;
$small-font-size:  $base-font-size * 0.875;
$base-line-height: 1.5;

$spacing-unit:     30px;

$text-color:       #111;
$background-color: #fdfdfd;
$brand-color:      #2a7ae2;

$grey-color:       #828282;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark:  darken($grey-color, 25%);

// Width of the content area
$content-width:    800px;

$on-palm:          600px;
$on-laptop:        800px;


// Use media queries like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit / 2;
//         padding-left: $spacing-unit / 2;
//     }
// }
@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}



// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "base",
        "layout",
        "syntax-highlighting",
        "theme"
;

